.services {
    box-shadow: inset 0 0 0 #ffffff;
    background-image: url("../img/layer-28-copy.png");
    background-position: 40% 32%;
    background-repeat: no-repeat;
    .container {
        @media #{$phone} {
            padding: 0;
        }

    }
}
.service {
    display: flex;
    @media #{$tablet} {
        padding-top: 50px;
    }
    @media #{$mobile} {
        flex-direction: column;
        padding-top: 2px;
    }
    &__item {
        position: relative;
        width: 25%;
        min-height: 337px;
        border: none;
        padding: 0;
        background-size: 100%;
        background-repeat: no-repeat;
        @media #{$mobile} {
            width: 100%;
            -webkit-background-size: 102%;
            background-size: 102%;
        }
    }
}
.serv {
    &__title {
        position: absolute;
        top: -5px;
        width: 100%;
        padding: 17px;
        font-size: 24px;
        text-transform: uppercase;
        color: $white;
        background-color: $dark-green;
        opacity: 0.9;
    }
    &__subtitle {
        position: absolute;
        bottom: 34px;
        width: 100%;
        padding: 5px 15px;
        font-size: 16px;
        font-weight: 400;
        font-family: 'Roboto Condensed', sans-serif;
        color: $white;
        background-color: $dark-green;
        opacity: 0.9;
        @media #{$tablet} {
            bottom: 80px;
        }
    }
    .b-first {
        @media #{$tablet} {
            bottom: 60px;
        }

    }
}


